	data() {
		return {
			goodsList:[
				{id:1,img:'/static/img/goods/p1.jpg',name:'商品标题商品标题商品标题商品标题商品标题商品标题商品标题商品标题商品标题商品标题',spec:'规格:S码',price:127.5,number:1,selected:false},
				{id:2,img:'/static/img/goods/p1.jpg',name:'商品标题商品标题商品标题商品标题商品标题商品标题商品标题商品标题商品标题商品标题',spec:'规格:S码',price:127.5,number:1,selected:false},
				{id:3,img:'/static/img/goods/p1.jpg',name:'商品标题商品标题商品标题商品标题商品标题商品标题商品标题商品标题商品标题商品标题',spec:'规格:S码',price:127.5,number:1,selected:false},
			],
			shopList:[
				{id:1,name:"冰鲜专卖店",img:"/static/img/shop/1.jpg"},
				{id:2,name:"果蔬天下",img:"/static/img/shop/2.jpg"},
				{id:3,name:"办公耗材用品店",img:"/static/img/shop/3.jpg"},
				{id:4,name:"天天看好书",img:"/static/img/shop/4.jpg"}
			],
			headerTop:0,
			//控制滑动效果
			typeClass:'goods',
			subState:'',
			theIndex:null,
			oldIndex:null,
			isStop:false
		}
	},
	onPageScroll(e){
		
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
	    setTimeout(function () {
	        uni.stopPullDownRefresh();
	    }, 1000);
	},
	onLoad() {
		//兼容H5下排序栏位置










	},
	methods: {
		switchType(type){
			if(this.typeClass==type){
				return ;
			}
			uni.pageScrollTo({
				scrollTop:0,
				duration:0
			})
			this.typeClass = type;
			this.subState = this.typeClass==''?'':'show'+type;
			setTimeout(()=>{
				this.oldIndex = null;
				this.theIndex = null;
				this.subState = this.typeClass=='goods'?'':this.subState;
			},200)
		},
		//控制左滑删除效果-begin
		touchStart(index,event){
			//多点触控不触发
			if(event.touches.length>1){
				this.isStop = true;
				return ;
			}
			this.oldIndex = this.theIndex;
			this.theIndex = null;
			//初始坐标
			this.initXY = [event.touches[0].pageX,event.touches[0].pageY];
		},
		touchMove(index,event){
			//多点触控不触发
			if(event.touches.length>1){
				this.isStop = true;
				return ;
			}
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];
			
			if(this.isStop||Math.abs(moveX)<5){
				return ;
			}
			if (Math.abs(moveY) > Math.abs(moveX)){
				// 竖向滑动-不触发左滑效果
				this.isStop = true;
				return;
			}
			
			if(moveX<0){
				this.theIndex = index;
				this.isStop = true;
			}else if(moveX>0){
				if(this.theIndex!=null&&this.oldIndex==this.theIndex){
					this.oldIndex = index;
					this.theIndex = null;
					this.isStop = true;
					setTimeout(()=>{
						this.oldIndex = null;
					},150)
				}
			}
		},
		
		touchEnd(index,$event){
			//解除禁止触发状态
			this.isStop = false;
		},
		
		//删除商品
		deleteCoupon(id,List){
			let len = List.length;
			for(let i=0;i<len;i++){
				if(id==List[i].id){
					List.splice(i, 1);
					break;
				}
			}
			this.oldIndex = null;
			this.theIndex = null;
		},
		
		discard() {
			//丢弃
		}
		
		
	}
}
